/* src/components/Footer.css */

.footer {
    background-color: #474747;
    color: #fff;
    padding: 40px 115px;
    text-align: left;
    padding-bottom: 20px;
}

.footer-sections {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-bottom: 20px;
}

.footer-section {
    flex: 1;
    margin: 0 15px;
    max-width: 250px;
   
}

.footer-section h3 {
    font-size: 30px;
    margin-bottom: 10px;
    color: #f5f1f1;
}

.footer-section p,
.footer-section ul,
.footer-section a {
    color: #ddd;
    font-size: 14px;
    margin-bottom: 10px;
   
}
.footer-section p{
    padding-top: 30px;

}  
.footer-social-icons{
    padding-top: 25px;
}
.footer-social-icons a img {
    width: 24px;
    height: 24px;
    margin-right: 10px;
    background-color: #AD343E;
    border-radius: 50%;
    padding: 10px;
    
    
}

.footer-links {
    list-style: none;
    padding: 0;
    line-height: 1.8;
    padding-top: 20px;
}

.footer-instagram-gallery {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 15px;
}

.footer-instagram-gallery img {
    width: 130px;
    height: 130px;
    border-radius: 5px;
}

.footer-links li {
    margin-bottom: 15px;
}

.footer-links li a {
    color: #ddd;
    text-decoration: none;
}

.footer-links li a:hover {
    color: #640f0f;
}

.footer-section p+p {
    margin-top: 5px;
}

.footer-section ul {
    margin-top: 15px;
}

.footer-section p {
    margin: 5px 0;
    margin-bottom: -10px;
}

.footer-copyright {
    font-size: 14px;
    color: #bbb;
    margin-top: 10px;
    text-align: center;
    padding-bottom: 0px;
}
.footer-divider {
    border: none;
    border-top: 2px solid #737272;
    margin: 20px 0;
    width: 1280px;
    opacity: 0.6;
}