.home {
    height: 655px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    background-color: #000;
}

.home-content {
    max-width: 600px;
    padding-left: 150px;
   
    
}

.home-title {
    font-size: 80px;
    margin-bottom: 0px;
    color: aliceblue;
  font-family: "Playfair Display", serif;
    font-weight: 400;
    font-style: normal;
    text-align: center;
    
}

.home-buttons {
    display: flex;
    gap: 10px;
    margin-top: 80px;
    font-family: "Playwrite GB S", cursive;
}

.home-button {
    color: white;
    padding: 10px 20px;
    text-decoration: none;
    border-radius: 25px;
    transition: background-color 0.3s;
   
}

.home-button.reservation {
    background-color: #AD343E;
}

.home-button.reservation:hover {
    background-color: #AD343E;
}

.home-button.menu {
    background-color: black;
    border: 2px solid #fdfafa;

}

.home-button.menu:hover {
    background-color: #524f4f;
}

.image-right {
    width: 700px;
    height: 608px;
    padding-top: 25px;
    display: flex;
justify-content: flex-end;
   
}
/* 2eme section */
.offers-section {
    text-align: center;
    padding: 50px 20px;
    background-color: #ffffff;
    padding-bottom: 60px;
}

.offers-title {
    font-size: 40px;
    padding-top: 10px;
    color: #333;
font-family: "Playfair Display", serif;
    font-weight: 200;
letter-spacing: 0.1cap;  
}


.offers-cards {
    display: flex;
    padding-top: 40px;
    justify-content: center;
    gap: 20px;
    flex-wrap: wrap;
    font-family: "Playfair Display", serif;
}
